#wrapper {
  display: grid;
  grid-template-rows: 1fr 5fr;
  height: 100vh;
}

#title {
  font: bold 54px "Open Sans", sans-serif;
  text-align: center;
  padding: 12px 0;
  margin: auto 0;
}

.nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: auto;
}

.nav__fragment {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 230px;
}

.nav__fragment__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  border-radius: 40px 40px 0 0;
}

.nav__fragment__bg.about {
  background: url("../assets/question-marks.jpg") no-repeat center center / cover;
}

.nav__fragment__bg.database {
  background: url("../assets/yerba.jpg") no-repeat center center / cover;
}

.nav__fragment__bg.api {
  background: url("../assets/server-room.jpg") no-repeat center center / cover;
}

.nav__fragment--text {
  position: relative;
  font: bold 18px "Open Sans", sans-serif;
  background: rgba(222, 184, 135, 0.6);
  border-radius: 14px;
  padding: 24px 16px;
  margin: 46px;
  text-align: center;
  cursor: pointer;
  transition:
    0.4s background,
    0.45s box-shadow;
}

.nav__fragment--text:hover {
  background: rgba(222, 184, 135, 0.9);
  box-shadow: 0 0 18px black;
}

.nav__fragment--title {
  margin-bottom: 10px;
}

@media(max-width: 999px) {
  .nav {
    grid-template-columns: 1fr;
  }

  .nav__fragment__bg {
    border-radius: 0;
  }

  .nav__fragment__bg.about {
    border-radius: 40px 40px 0 0;
  }
}