@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
#wrapper {
  display: grid;
  grid-template-rows: 1fr 5fr;
  height: 100vh;
}

#title {
  font: bold 54px "Open Sans", sans-serif;
  text-align: center;
  padding: 12px 0;
  margin: auto 0;
}

.nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: auto;
}

.nav__fragment {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 230px;
}

.nav__fragment__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  border-radius: 40px 40px 0 0;
}

.nav__fragment__bg.about {
  background: url(/static/media/question-marks.af4f46be.jpg) no-repeat center center / cover;
}

.nav__fragment__bg.database {
  background: url(/static/media/yerba.0359d372.jpg) no-repeat center center / cover;
}

.nav__fragment__bg.api {
  background: url(/static/media/server-room.ece56028.jpg) no-repeat center center / cover;
}

.nav__fragment--text {
  position: relative;
  font: bold 18px "Open Sans", sans-serif;
  background: rgba(222, 184, 135, 0.6);
  border-radius: 14px;
  padding: 24px 16px;
  margin: 46px;
  text-align: center;
  cursor: pointer;
  transition:
    0.4s background,
    0.45s box-shadow;
}

.nav__fragment--text:hover {
  background: rgba(222, 184, 135, 0.9);
  box-shadow: 0 0 18px black;
}

.nav__fragment--title {
  margin-bottom: 10px;
}

@media(max-width: 999px) {
  .nav {
    grid-template-columns: 1fr;
  }

  .nav__fragment__bg {
    border-radius: 0;
  }

  .nav__fragment__bg.about {
    border-radius: 40px 40px 0 0;
  }
}
.about--title {
  font: bold 40px "Open Sans", sans-serif;
}

.about--p {
  font: 18px "Open Sans", sans-serif;
  margin: 20px 0;
}

.database--title {
  font: bold 40px "Open Sans", sans-serif;
  text-align: center;
  margin: 25px 0 10px 0;
}

.database__loading {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
}

.database__loading--img {
  height: 100px;
  -webkit-animation: spin 1.2s infinite;
          animation: spin 1.2s infinite;
}

@-webkit-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(-360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(-360deg); }
}

.database--list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 30px;
  -moz-column-gap: 30px;
       column-gap: 30px;
  margin: 30px;
  list-style-type: none;
}

.database--item {
  background-color: rgb(185, 153, 110);
  margin-bottom: 8px;
  border-radius: 8px;
  padding: 8px;
}

@media(max-width: 799px) {
  .database--list {
    grid-template-columns: 1fr;
  }
}

/* Globals */
* {
  padding: 0; margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans";
}

body {
  background: burlywood;
  overflow-x: hidden;
  width: 100vw;
}

a {
  text-decoration: none;
  color: #000;
  color: initial;
}

.link {
  color: darkorchid;
}

.center-div {
  width: 100vw;
  text-align: center;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}

.error--title {
  font: bold 40px "Open Sans", sans-serif;
}

