.database--title {
  font: bold 40px "Open Sans", sans-serif;
  text-align: center;
  margin: 25px 0 10px 0;
}

.database__loading {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
}

.database__loading--img {
  height: 100px;
  animation: spin 1.2s infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(-360deg); }
}

.database--list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 30px;
  margin: 30px;
  list-style-type: none;
}

.database--item {
  background-color: rgb(185, 153, 110);
  margin-bottom: 8px;
  border-radius: 8px;
  padding: 8px;
}

@media(max-width: 799px) {
  .database--list {
    grid-template-columns: 1fr;
  }
}
