@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

/* Globals */
* {
  padding: 0; margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans";
}

body {
  background: burlywood;
  overflow-x: hidden;
  width: 100vw;
}

a {
  text-decoration: none;
  color: initial;
}

.link {
  color: darkorchid;
}

.center-div {
  width: 100vw;
  text-align: center;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}

.error--title {
  font: bold 40px "Open Sans", sans-serif;
}

/* Importing .css files */
@import "./home.css";
@import "./about.css";
@import "./database.css";
